/* Colors */

$blue: #002c64;
$grey: #bababa;
$black: #2e2e2e;
$blacker: #565656;
$greyer: #707070;

@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@300;500;700&display=swap");

/* Mixins */

@mixin font-style {
  font-family: Roboto;
  font-style: normal;
}

@mixin container-shadow {
  box-shadow: 0px 4px 31px rgba(0, 0, 0, 0.15);
  border-radius: 30px;
}

@mixin flex-and-center {
  display: flex;
  align-items: center;
  text-align: center;
  justify-content: center;
}

@mixin flex-and-space-between {
  display: flex;
  align-items: center;
  text-align: center;
  justify-content: space-between;
}

@mixin header-text {
  font-weight: 500;
  font-size: 18px;
  line-height: 21px;
}

/* Chat Widgets Style */

.chatWidgetRoot {
  position: fixed;
  right: 30px;
  bottom: 30px;
  z-index: 1;

  button,
  input[type="submit"],
  input[type="reset"] {
    color: inherit;
    border: none;
    padding: 0;
    font: inherit;
    cursor: pointer;
    outline: inherit;
  }
}

.widgetTogglerParent {
  display: flex;
  justify-content: center;
  align-items: center;
  max-width: 206px;
  height: 51px;
  z-index: 1;
  margin-top: 30px;
  margin-left: auto;

  button {
    z-index: 2;
  }
}

.chatWidgetBackgroundContainer {
  display: inline-block;
  position: sticky;
  width: 100%;
  // padding-bottom: 100%;
  vertical-align: middle;
  overflow: hidden;
  z-index: 0;
  height: inherit;

  svg {
    display: inline-block;
    position: absolute;
    top: 0;
    left: 0;
  }
}

.widgetControlFadeOut {
  transition: transform 0.5s ease, opacity 0.1s ease;
  transform: translateX(500px);
  opacity: 0;
}

.widgetControlFadeIn {
  transition: transform 0.3s ease, opacity 0.3s ease;
  transform: translateX(0px);
  opacity: 1;
}

.widgetTogglerContainer {
  z-index: 1;
  background: white;
  background: #ffffff;
  box-shadow: 0px 0px 17px rgba(0, 0, 0, 0.25);
  border-radius: 30px;
  margin-right: -50px;

  p {
    font-family: Roboto;
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    line-height: 19px;
    text-transform: uppercase;
    color: #002c64;
    padding-left: 27px;
    padding-right: 63px;
    cursor: default;
    overflow: hidden;
    z-index: 1;
    white-space: nowrap;
  }
}

.widgetToggler {
  background: $blue;
  border-radius: 50%;
  aspect-ratio: 1;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  // margin: 1vw 60vw auto;
  float: right;
  background: #0054a4;
}

.chatWidgetContainer {
  @include font-style;
  @include container-shadow;
  //max-height: 704px;
  height: 704px;
  max-height: 80vh;
  max-width: 376px;
  background: #ffffff;
  margin: auto;
  position: relative;
  z-index: 1;
  opacity: 0;
  background: linear-gradient(180deg, #ffffff 0%, #f5f9ff 100%);
  /*background-image: url(/media/background-sb.svg);*/
  box-shadow: 0px 4px 31px rgba(0, 0, 0, 0.15);
  border-radius: 30px;

  .inputFadingBackground {
    position: absolute;
    bottom: -4px;
    display: block;
    width: 100%;
    height: 160px;
    background-image: linear-gradient(
      to bottom,
      rgba(255, 255, 255, 0),
      rgba(255, 255, 255, 0.9) 100%
    );
    z-index: 1;
    pointer-events: none;
    border-radius: 30px;
  }

  .chatWidgetHeader {
    @include flex-and-space-between;
    height: 70px;
    border-radius: 30px 30px 0px 0px;
    background: #002c64;
    // Header padding
    padding-left: 29px;
    padding-right: 29px;
    padding-bottom: 27px;
    border-radius: 30px 30px 0px 0px;

    .headerItemGroup {
      display: flex;
      align-items: center;
      .chatMenu {
        display: flex;
        flex-flow: column;
        margin-right: 19px;
        svg:not(:last-child) {
          margin-bottom: 5px;
        }
      }
    }
    .logoContainer {
      background: white;
      border-radius: 50%;
      padding: 0.5em;
      margin-right: 19px;
      svg {
        display: block;
      }
    }

    .chatHeaderText {
      @include header-text;
      color: #ffffff;
    }

    .headerControls {
      display: flex;
      align-items: center;
      svg:not(:last-child) {
        margin-right: 19px;
      }
    }
  }

  .chatWidgetContent {
    display: flex;
    flex-flow: column;
    align-items: center;
    background: linear-gradient(180deg, #ffffff 0%, #f5f9ff 100%);
    border-radius: 30px 0px 0px 0px;
    position: relative;
    top: -30px;
    //padding-top: 3vw;
    height: 91%;
    border-radius: 30px;
    overflow-y: auto;
    overflow-x: hidden;

    &::-webkit-scrollbar {
      background-color: #fff;
      background-color: rgba(255, 255, 255, 0);
      width: 16px;
    }

    &::-webkit-scrollbar-track {
      //background-color: #fff;
      background-color: rgba(255, 255, 255, 0);
      margin-top: 15px;
      margin-bottom: 15px;
    }

    &::-webkit-scrollbar-thumb {
      background-color: #babac0;
      border-radius: 16px;
      border: 4px solid #fff;
      background: #c4c4c4;
      border-radius: 50px;
    }

    .widgetMessagesContainer {
      .widgetMsgWrapper:nth-child(1) {
        margin-top: 10%;
      }
      padding-bottom: 70px;
      overflow-y: scroll;
      z-index: 1;
      margin-bottom: 20px;

      &::-webkit-scrollbar {
        background-color: #fff;
        background-color: rgba(255, 255, 255, 0);
        width: 16px;
      }

      &::-webkit-scrollbar-track {
        //background-color: #fff;
        background-color: rgba(255, 255, 255, 0);
        margin-top: 15px;
        margin-bottom: 15px;
      }

      &::-webkit-scrollbar-thumb {
        background-color: #babac0;
        border-radius: 16px;
        border: 4px solid #fff;
        background: #c4c4c4;
        border-radius: 50px;
      }
    }

    .pageFlipImage {
      position: absolute;
      bottom: 1px;
      left: 0;
      width: 100%;
      height: 100%;
      opacity: 0;
      z-index: 0;
    }

    .widgetMsgWrapper {
      display: flex;
      align-items: center;
      flex-wrap: wrap;

      .logoContainerBlack {
        background: #ffffff;
        border: 2px solid #002c64;
        border-radius: 50%;
        padding: 0.3em;
        margin-right: 19px;
        align-self: flex-end;
        position: relative;
        left: 20px;
        top: -10px;
        svg {
          display: block;
          path {
            fill: $blue;
          }
        }
      }

      .avatar {
        left: 0;
        background: $blue;
        top: -15px;
        border-radius: 50%;
        padding: 0.5em;
        align-self: flex-end;
        position: relative;
        height: 16px;
        width: 16px;
      }

      .widgetQuickRepliesContainer {
        margin-left: 20%;
        margin-top: 8px;
        z-index: 1;
        padding-bottom: 15px;
        max-width: 64%;

        .widgetQuickReply {
          border: 1px solid $blue;
          box-sizing: border-box;
          border-radius: 15px;
          font-weight: 500;
          font-size: 14px;
          line-height: 16px;
          color: $blue;
          padding: 6px;
          margin-right: 5px;
          padding-right: 16px;
          padding-left: 16px;
          transition: 0.7s;

          border: 1px solid #0054a4;
          box-sizing: border-box;
          border-radius: 15px;

          font-family: Roboto;
          font-style: normal;
          font-weight: 500;
          font-size: 14px;
          line-height: 16px;

          color: #0054a4;

          &:hover {
            color: #fff;
            background: $blue;
          }
        }
      }
    }

    .msgWrapperRight {
      align-self: flex-end;
      margin-right: 23px;
      justify-content: flex-end;
    }

    .widgetMessageBody {
      max-width: 65%;
      margin: 1rem;
      text-align: center;
      padding: 10px;
      position: relative;
      .widgetUsername {
        position: absolute;
        top: -15px;
        font-weight: 300;
        font-size: 10px;
        line-height: 12px;
        color: $greyer;
      }
    }

    .responseMsg {
      background: #f1f1f1;
      border-radius: 15px 15px 15px 0px;
      align-self: flex-start;

      background: #ffffff;
      box-shadow: 0px 4px 30px rgba(0, 0, 0, 0.15);
      border-radius: 15px 15px 15px 0px;
      box-sizing: border-box;

      .text {
        font-weight: 300;
        font-size: 14px;
        line-height: 16px;
        color: #0a0a0a;
        text-align: left;
        font-family: Roboto;
        font-style: normal;
        font-weight: 300;
        font-size: 14px;
        line-height: 16px;
        display: flex;
        align-items: center;
        overflow-wrap: anywhere;
        color: #0a0a0a;
      }
    }

    .requestMsg {
      background: #0054a4;
      border-radius: 15px 15px 0px 15px;

      align-self: flex-end;

      .textTyping {
        font-weight: 900;
        font-size: 14px;
        line-height: 16px;
        color: #ffffff;
      }

      .text {
        font-family: Roboto;
        font-style: normal;
        font-weight: 300;
        font-size: 14px;
        line-height: 16px;
        display: flex;
        align-items: center;
        text-align: center;
        color: #ffffff;
      }
    }
  }

  .chatWidgetFooter {
    @include flex-and-center;
    flex-flow: column;
    font-weight: 300;
    font-size: 11px;
    line-height: 13px;
    color: $grey;
    width: 100%;
    position: absolute;
    bottom: 5%;
    z-index: 2;
    .widgetForm {
      display: flex;
      justify-content: center;
      width: 90%;

      .widgetControlsSend {
        border-radius: 50%;
        background: linear-gradient(
          150.12deg,
          #002c64 -54.63%,
          #0054a4 111.84%
        );
        display: flex;
        align-items: center;
        width: 40px;
        height: 40px;

        svg {
          display: block;
          margin: auto;
          padding: 10px;
        }
      }

      .widgetControlsInput {
        font-weight: 300;
        font-size: 12px;
        line-height: 14px;
        color: $blacker;
        background: transparent;
        border: none;
        border-bottom: 1px solid $blacker;
        box-sizing: border-box;
        padding: 9px 16px 9px 10px;
        width: 80%;
        margin-right: 11px;

        padding-left: 0;
        padding-bottom: 2px;
      }
    }
  }
}

/* Animation classes */

@mixin fade-in($waitTime) {
  animation: wait #{$waitTime}, fade-in 800ms #{$waitTime};
}

.fadeOut {
  animation: fadeOut ease 0.35s forwards;
  -webkit-animation: fadeOut ease 0.35s forwards;
  -moz-animation: fadeOut ease 0.35s forwards;
  -o-animation: fadeOut ease 0.35s forwards;
  -ms-animation: fadeOut ease 0.35s forwards;
}
@keyframes fadeOut {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

@-moz-keyframes fadeOut {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

@-webkit-keyframes fadeOut {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

@-o-keyframes fadeOut {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

@-ms-keyframes fadeOut {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

.fadeIn {
  animation: fadeIn ease 0.7s forwards;
  -webkit-animation: fadeIn ease 0.7s forwards;
  -moz-animation: fadeIn ease 0.35s forwards;
  -o-animation: fadeIn ease 0.35s forwards;
  -ms-animation: fadeIn ease 0.35s forwards;
}
@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@-moz-keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@-webkit-keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@-o-keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@-ms-keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.transformTranslateQuickReplies {
  @include fade-in("1000ms");
  transition: 1s;
}

@keyframes shrinkToLeft {
  0% {
    width: 100%;
    left: 0%;
  }
  100% {
    width: 0%;
    left: 100%;
  }
}
